import React, { Component } from "react";
import "./dashboard.css";
import DataTable from "../data-table/data-table";
import Visa from "../visa/visa";
class Dashboard extends Component {
  state = {};
  render() {
    return (
      <div className='container-fluid main-dashboard'>
        <div>
          <div className="main-data">
            <div className='row'>
              {/* // sort box  */}
              <div className='col-lg-4 sort'>
                <h4>Task Activity</h4>
                <ul className='list-unstyled btn-list'>
                  <li className='d-inline-block'>
                    <button className='btn-all'  >All</button>
                  </li>
                  <li className='d-inline-block'>
                    <button>1D</button>
                  </li>
                  <li className='d-inline-block'>
                    <button>1M</button>
                  </li>
                  <li className='d-inline-block'>
                    <button>1Y</button>
                  </li>
                </ul>
              </div>
              <div className='col-lg-4 col-md-6 col-6 t-check'>
                {/* total checkout success box */}
                <div>
                  <span className='main'>
                    <span className='sp-left float-left'>
                      <span className='number d-block'>13</span>
                      <span className='title'>Total Checkouts</span>
                    </span>
                    <span className='span-right img-success float-right'>
                      <img src={require("../../assets/images/success.png")} alt='' />
                    </span>
                  </span>
                </div>
              </div>
              {/* total  failed box  */}
              <div className='col-lg-4 col-md-6 col-6 t-fail'>
                <div>
                  <span className='main'>
                    <span className='sp-uni sp-left float-left'>
                      <span className='number d-block'>0</span>
                      <span className='title'>Total Failure</span>
                    </span>
                    <span className='sp-uni span-right float-right'>
                      <img src={require("../../assets/images/faild.png")} alt='' />
                    </span>
                  </span>
                </div>
              </div>
            </div>
            <DataTable />
          </div>
          <div>
            <Visa />
          </div>
        </div>
      </div>
    );
  }
}

export default Dashboard;
