import React, { Component } from "react";
import "./logo.css";
class Logo extends Component {
  state = {
    On: " out-w ",
    Off: " "
  };
  onFunction = () => {
    this.setState({
      On: " out-w ",
      Off: " text-muted "
     
    });
  };
  offFunction = () => {
    this.setState({
      On: " img-muted ",
      Off: "  text-white off-btn-w"
     
    });
  };
  render() {
    return (
      <div className='pos-img'>
        <img className='logo-top' src={require("../../assets/images/logo.png")} alt='' />
        <img className='left-logo' src={require("../../assets/images/logo-small.png")} alt='' />
        <div className='logout'>
          <button onClick={this.onFunction} className={"out " + this.state.On }></button>
          <div className="l-out">
            <button onClick={this.offFunction} className={"off-btn " + this.state.Off}>
              <i className='fas fa-power-off'></i>
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default Logo;
