import React, { Component } from "react";
import "./license.css";
import Download from "../download/download";
class LicenseKey extends Component {
  state = {
    type: "password",
    eye: "fa-eye"
  };

  handelInput = () => {
    this.setState({
      type: this.state.type === "password" ? "text" : "password",
      eye: this.state.eye === "fa-eye" ? "fa-eye-slash" : "fa-eye"
    });
  };

  // control dialog when click active btn
  handelAcBtn = () => {
    this.setState({
      btnSca: " btn-sca ",
      hideDia: " d-block ",
      showDia: " d-none ",
      SuccessDia: " d-none ",
      hideDialog: " block ",
      hideSpin: ""
    });
  };
  // when click on unbind btn
  handelBindBtn = () => {
    this.setState({
      btnScale: " btn-sca ",
      hideDia: " d-block ",
      showDia: " d-none ",
      SuccessDia: " d-none ",
      hideDialog: " block ",
      hideSpin: " "
    });
  };
  // this reset btn animation of de active and unbind
  resetBtn = () => {
    this.setState({ btnSca: " ", btnScale: " " });
  };
  // delay with dialog box and change text of side  de active btn
  handelDialog = () => {
    this.setState({
      hideDia: " d-none ",
      showDia: " dis-blo",
      btnSca: "   ",
      btnScale: " ",
    });
    setTimeout(() => {
      this.setState({ hideSpin: " d-none ", SuccessDia: " d-block" });
    }, 2000);
    setTimeout(() => {
      this.setState({ hideDialog: " d-noneDia " });
    }, 4000);
  };
  rehandelDialog = () => {
    this.setState({
      hideDia: " d-none ",
      showDia: " dis-blo",
      btnSca: "   ",
      btnScale: " ",
    });
    setTimeout(() => {
      this.setState({ hideSpin: " d-none ", SuccessDia: " d-block" });
    }, 2000);
    setTimeout(() => {
      this.setState({ hideDialog: " d-noneDia ", });
    }, 4000);
  };
  render() {
    return (
      <div>
        <div className='keys'>
          <ul className='list-unstyled'>
            {/* license key section  */}
            <li>
              <div className='row w-100 m-0  l-key-top'>
                {/* label of license key  */}
                <label htmlFor='key'>License Key</label>
                <div className='d-flex justify-content-center w-100'>
                  <div className='pl-0  d-flex justify-content-center field'>
                    <img className='img-key' src={require("../../assets/images/key.png")} alt='' />
                    <div className='list-content'>
                      <input
                        id='key'
                        type={this.state.type}
                        className=''
                        defaultValue='0123456789'
                      />
                      <i onClick={this.handelInput} className={"far " + this.state.eye}></i>
                    </div>
                  </div>
                  <div className='de-act'>
                    <button
                      id='deActivateBtn'
                      className={"deactivated " + this.state.btnSca}
                      onClick={this.handelAcBtn}>
                      DEACTIVE
                    </button>
                  </div>
                </div>
              </div>
            </li>
            {/* Discord Id section  */}
            <li className='second-li'>
              <div className='row w-100 m-0 l-key-bottom '>
                <div className='d-flex justify-content-center w-100'>
                  <div className='pl-0  d-flex left-disc '>
                    <img
                      className='img-key last-img'
                      src={require("../../assets/images/discoard.png")}
                      alt=''
                    />
                    <div className='ml-3'>
                      <span className='d-block dis-id'>Discord ID</span>
                      <span className='name'>Jhondoe873</span>
                    </div>
                  </div>
                  <div className='unbind'>
                    <button
                      onClick={() => {
                        this.handelBindBtn();
                      }}
                      id='deActivateBtn2'
                      className={"deactivated " + this.state.btnScale}>
                      UNBIND
                    </button>
                  </div>
                </div>
              </div>
            </li>
          </ul>
          <div>
            <Download />
          </div>
        </div>

        {/* ========================================================== */}
        {/* Modal Dialog  first dialog  */}
        <div
          className={" modal first-dialog fade " + this.state.hideDialog}
          id='exampleModalCenter'
          tabIndex='-1'
          role='dialog'
          aria-labelledby='exampleModalCenterTitle'
          aria-hidden='true'>
          <div className='modal-dialog modal-dialog-centered' role='document'>
            <div className='modal-content py-4'>
              <div className={"options p-4 " + this.state.hideDia}>
                <div className=''>
                  <h5 className=' mb-5 text-center font-weight-bold text-white'>
                    Are you sure to deactivate your <br />
                    license?
                  </h5>
                </div>
                <div className='mb-2'>
                  <button
                    type='button'
                    className='btn-no bg-transparent btn btn-secondary btn-block font-weight-bold py-2 btn-no '
                    data-dismiss='modal'
                    onClick={this.resetBtn}>
                    No
                  </button>
                </div>
                <div>
                  <button
                    onClick={this.handelDialog}
                    type='button'
                    className='bg-white   btn-yes btn  btn-block font-weight-bold py-2 '>
                    Yes
                  </button>
                </div>
              </div>
              <div className={"py-5 text-center " + this.state.showDia + this.state.hideSpin}>
                <div>
                  <h5 className='text-center text-white mb-5'>Please wait</h5>
                  <img
                    className='spinner'
                    src={require("../../assets/images/spinner.png")}
                    alt='new'
                  />
                </div>
              </div>
              <div className={" text-center  " + this.state.SuccessDia}>
                <div className='mb-3'>
                  <span className='okay-h'>
                    <i className='fas fa-check fa-2x text-white'></i>
                  </span>
                </div>
                <h3 className='text-white font-weight-bold'>Deactivate Successfully</h3>
              </div>
            </div>
          </div>
        </div>

        {/* Dialog 2  */}

        <div
          className={" modal second-dialog  fade "}
          id='exampleModalCenter2'
          tabIndex='-1'
          role='dialog'
          aria-labelledby='exampleModalCenterTitle'
          aria-hidden='true'>
          <div className='modal-dialog  modal-dialog-centered' role='document'>
            <div className='modal-content py-4'>
              <div className={"options p-4 " + this.state.hideDia}>
                <div className=''>
                  <h5 className=' mb-5 text-center font-weight-bold text-white'>
                    Are you sure to unbind your <br />
                    discoard account
                  </h5>
                </div>
                <div className='mb-2'>
                  <button
                    type='button'
                    className='btn-no bg-transparent btn btn-secondary btn-block font-weight-bold py-2 btn-no '
                    data-dismiss='modal'
                    onClick={this.resetBtn}>
                    No
                  </button>
                </div>
                <div>
                  <button
                    onClick={() => {
                      // onUnBind();
                      this.rehandelDialog();
                    }}
                    type='button'
                    className='bg-white   btn-unbind-yes btn  btn-block font-weight-bold py-2 '>
                    Yes
                  </button>
                </div>
              </div>
              <div className={"py-5 text-center " + this.state.showDia + this.state.hideSpin}>
                <div>
                  <h5 className='text-center text-white mb-5'>Please wait</h5>
                  <img
                    className='spinner'
                    src={require("../../assets/images/spinner.png")}
                    alt='new'
                  />
                </div>
              </div>
              <div className={" text-center  " + this.state.SuccessDia}>
                <div className='mb-3'>
                  <span className='okay-h'>
                    <i className='fas fa-check fa-2x text-white'></i>
                  </span>
                </div>
                <h3 className='text-white font-weight-bold'>Unbinded Successfully</h3>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LicenseKey;
