import $ from 'jquery';

$(function () {
  $("input[name='cardNumber']").keydown(function () {

    var curChr = this.value.length;
    var curVal = $(this).val();
    if (curChr === 4) {
      $("input[name='cardNumber']").val("" + curVal + "  ");
    } else if (curChr === 10) {
      $("input[name='cardNumber']").val(curVal + "  ");
    } else if (curChr === 16) {
      $("input[name='cardNumber']").val(curVal + "  ");
    }
    $('.modal-backdrop').css('display', 'none');
  })


  // toggle color between buttons
  $('.btn-list li button').click(function () {
    $(this).addClass('btn-all').parent().siblings().find('button').removeClass('btn-all')
  })

  $('.s-act').hide();
  $('.btn-download').click(function () {
    setTimeout(() => {
      $('.s-act').slideDown()
    }, 300)
    setTimeout(() => {
      $('.s-act').slideUp()
    }, 2500)
  })





  // =========================== control dialog ===================================
  // this is deactivated btn 
  $('#deActivateBtn').click(function () {
    setTimeout(function () {

      $('.modal-backdrop').css('display', 'block');
      $('#exampleModalCenter').css('display', 'block !important');
      $('.fade:not(.show)').css('opacity', '1');
      $(this).removeClass('btn-sca');
      $('#exampleModalCenter').modal().show();
      $('.modal-open').css('overflow', 'hidden');
    }, 300);
  })
  // this is unbind btn 

  $('#deActivateBtn2').click(function () {
    setTimeout(function () {
      $('.modal-backdrop').css('display', 'block');
      $('#exampleModalCenter2').css('display', 'block !important');
      $('.fade:not(.show)').css('opacity', '1');
      $(this).removeClass('btn-sca');
      $('#exampleModalCenter2').modal().show();
      $('.modal-open').css('overflow', 'hidden');
    }, 300);

  })
  //  this btn of yes after click deactivated and after click on in it it will show there is no computer 
  $('.btn-yes').click(function () {
    setTimeout(() => {
      $('.modal-backdrop').css('display', 'none');
      $('#exampleModalCenter').css('display', 'none');
      $('.modal-open').css('overflow-y', 'auto');
    }, 4300);
  })
  $('.btn-unbind-yes').click(function () {
    setTimeout(() => {
      $('.modal-backdrop').css('display', 'none');
      $('#exampleModalCenter2').css('display', 'none');
      $('.modal-open').css('overflow-y', 'auto');
    }, 4300);
  })
  $('.btn-no').click(function () {
    $('.modal-backdrop').css('display', 'none');
    $('#exampleModalCenter2').css('display', 'none');
    $('#exampleModalCenter').css('display', 'none');
    $('.modal-open').css('overflow-y', 'auto');
  })

  // btn save visa card 
  $('.e-act').hide();
  $('#editBTN').click(function () {
    if ($(this).hasClass('btn-save')) {
      $('.e-act').slideToggle()
    }
    setTimeout(() => {
      $('.e-act').slideUp()
    }, 2000)
  })
})