import React, { Component } from "react";
import "./download.css";
class Download extends Component {
  state = {
    ops1: " ",
    ops2: " ",
    imgDis: " inline-block ",
    okDis: " none ",
    text: " Download MacOS ",
    ops12: " ",
    ops22: " ",
    imgDis2: " inline-block ",
    okDis2: " none ",
    text2: " Download Windows "
  };
  onDownload = e => {
    this.setState({
      ops1: 1,
      ops2: 0,
      overlayAnimation: " over-anim",
      text: " Downloading . . ."
    });
    setTimeout(() => {
      this.setState({ imgDis: " none ", okDis: " inline-block" });
    }, 2500);
    setTimeout(() => {
      this.setState({ text: " Downloaded " });
    }, 2600);
    setTimeout(() => {
      this.setState({
        ops1: " ",
        ops2: " ",
        imgDis: " inline-block ",
        okDis: " none ",
        overlayAnimation: " ",
        text: " Download MacOS ",
      });
    }, 4000);
  };

  onDownload2 = e => {
    this.setState({
      ops12: 1,
      ops22: 0,
      overlayAnimation2: " over-anim",
      text2: " Downloading . . ."
    });
    setTimeout(() => {
      this.setState({ imgDis2: " none ", okDis2: " inline-block" });
    }, 2500);
    setTimeout(() => {
      this.setState({ text2: " Downloaded " });
    }, 2600);
    setTimeout(() => {
      this.setState({
        ops12: " ",
        ops22: " ",
        imgDis2: " inline-block ",
        okDis2: " none ",
        overlayAnimation2: " ",
        text2: " Download MacOS "
      });
    }, 4000);
  };
  render() {
    return (
      <div>
        {/* title of download section  */}
        <h5 className='download-title'>Download Apps</h5>
        <div className='row download'>
          {/* first download button  */}
          <div className='col'>
            <button className='mac-btn btn-download' onClick={this.onDownload}>
              <div className='down-div' style={{ opacity: this.state.ops1 }}>
                <div className={"overlay " + this.state.overlayAnimation}></div>
                <img
                  src={require("../../assets/images/download.png")}
                  style={{ display: this.state.imgDis }}
                  alt=''
                />
                <div>
                  <i
                    className='fas fa-check fa-2x text-success'
                    style={{ display: this.state.okDis }}></i>
                </div>
              </div>
              <img
                className='mac-img'
                src={require("../../assets/images/mac.png")}
                alt='download'
                style={{ opacity: this.state.ops2 }}
              />
              <h5>{this.state.text}</h5>
            </button>
          </div>
          {/* second download section  */}
          <div className='col'>
            <button className='windows-btn btn-download' onClick={this.onDownload2}>
              <div className='down-div' style={{ opacity: this.state.ops12 }}>
                <div className={"overlay " + this.state.overlayAnimation2}></div>
                <img
                  src={require("../../assets/images/download.png")}
                  alt=''
                  style={{ display: this.state.imgDis2 }}
                />

                <div>
                  <i
                    className='fas fa-check fa-2x text-success'
                    style={{ display: this.state.okDis2 }}></i>
                </div>
              </div>
              <img
                src={require("../../assets/images/windows.png")}
                alt='download'
                style={{ opacity: this.state.ops22 }}
              />
              <h5>{this.state.text2}</h5>
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default Download;
