import React, { Component } from "react";
import "./visa.css";
import CustomizedSwitches from "./switch";
class Visa extends Component {
  state = {
    defaultValue: "3421  9303  8738  3221",
    save: true,
    text: "Edit",
    editCheck: true,
    btnEd: "btn-edit",
    testDis: true,
    icon: ' fa-edit'
  };

  editHandelBtn = () => {
    var test = this.state.editCheck;
    this.setState({ editCheck: !this.state.editCheck });

    if (test === true) {
      this.setState({
        text: "Save",
        btnEd: "btn-save",
        testDis: false,
        icon: ' fa-save'
      });
    } else {
      this.setState({
        text: "Edit",
        btnEd: "btn-edit",
        testDis: true,
        icon: ' fa-edit'
      });
    }
  };
  render() {
    return (
      <div className='total-visa'>
        <div>
          <button id="editBTN" onClick={this.editHandelBtn} className={" btn-visa " + this.state.btnEd}>
          <i className={"fas " + this.state.icon}></i> {this.state.text}
          </button>
        </div>
        <div className='row'>
          <div className='big-visa col-sm-8 text-center position-relative'>
            <div className='main-visa'>
              <picture>
                <source
                  media='(max-width: 768px )'
                  srcSet={require("../../assets/images/visa1.png")}
                />
                <source
                  media='(max-width: 992px )'
                  srcSet={require("../../assets/images/visa.png")}
                />
                <source
                  media='(max-width: 1100px )'
                  srcSet={require("../../assets/images/visa1.png")}
                />

                <img src={require("../../assets/images/visa.png")} alt='' />
              </picture>
              {/* main number of visa card */}
              <div className='form-h form-h1 '>
                <input
                  name='cardNumber'
                  className='card-input'
                  pattern='[\d| ]{16,22}'
                  type='text'
                  defaultValue={this.state.defaultValue}
                  disabled={this.state.testDis}
                />
              </div>
              {/* Expire date of visa card  */}
              <div className='form-h form-h2 form-ic'>
                <label htmlFor='' className='d-block'>
                  Expire Date
                </label>
                <img
                  className='img-icon float-left'
                  src={require("../../assets/images/date.png")}
                  alt=''
                />
                <input
                  type='text'
                  id='datepicker'
                  defaultValue='05/22'
                  className=' datepicker font-control card-number cvc exp-f float-left' 
                  disabled={this.state.testDis}
                />
              </div>
              {/* CVV date of visa card  */}
              <div className='form-h form-h3 form-ic'>
                <label htmlFor='' className='d-block'>
                  CVV
                </label>
                <img
                  className='img-icon float-left'
                  src={require("../../assets/images/lock.png")}
                  alt=''
                />
                <input
                  type='number float-left'
                  defaultValue='186'
                  className='font-control card-number cvc'
                  disabled={this.state.testDis}
                />
              </div>
            </div>
          </div>
          <div className='control-visa col-sm-4'>
            <div className='small-control'>
              <div className='text-center'>
                <img src={require("../../assets/images/dash-img.png")} alt='' />
              </div>
              <div className='auto-control'>
                <div>
                  <h5 className='text-white text-center'>Auto Renew</h5>
                  <p className=' text-center'>Renew license monthly</p>
                </div>
                <div className='text-center d-flex justify-content-center on-off'>
                  {/* on/ off button that give you ability to access visa card inputs  */}
                  <span>
                    <CustomizedSwitches />
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Visa;
